  padding-right: 0;
}

.section-title {
  font-family: var(--text-font-family);
  text-align: center;
  text-transform: uppercase;
  color: #ffa5ba;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 60px;
  padding-bottom: 15px;
  font-size: 18px;
  line-height: 21px;
}

.article {
  background-repeat: no-repeat;
  background-size: cover;
}

.article-flex {
  background-repeat: no-repeat;
  flex-direction: column;
  display: flex;
}

.article-flex__image {
  width: 280px;
  height: 279px;
  margin: 0 20px 40px;
}

.article__title {
  font-family: var(--text-font-family);
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--text-pink-main);
  margin: 0 64px 0 56px;
  padding-top: 0;
  padding-bottom: 32px;
  font-size: 34px;
  line-height: 39px;
}

.paragraph-flex {
  background-repeat: no-repeat;
  background-size: 280px;
  flex-direction: column;
  margin-top: 40px;
  margin-right: 80px;
  display: flex;
}

.paragraph-flex__paragraph1 {
  font-family: var(--alternate-text-font-family);
  letter-spacing: .04em;
  margin-top: 0;
  margin-bottom: 18px;
  margin-left: 20px;
  font-size: 12px;
  line-height: 162%;
}

.paragraph-flex__paragraph2 {
  font-family: var(--alternate-text-font-family);
  letter-spacing: .04em;
  color: #907e82;
  margin-top: 0;
  margin-left: 20px;
  padding-bottom: 20px;
  font-size: 12px;
  line-height: 162%;
}

.read-more-button {
  font-family: var(--alternate-text-font-family);
  text-align: center;
  color: var(--text-pink-main);
  width: 176px;
  height: 44px;
  background: #fff;
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
  margin-left: 20px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.read-more-button__btn-svg {
  width: 4.43px;
  height: 8px;
  padding-left: 13.62px;
}

.read-more-button:hover {
  background-color: #ecebeb;
}

.features-list {
  flex-direction: column;
  gap: 32px;
  margin-left: 20px;
  display: flex;
}

.features-list:first-child {
  margin-left: 20px;
}

.features-list__svg {
  width: 50px;
  height: 50px;
}

.features-list__heading {
  font-family: var(--text-font-family);
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--text-pink-main);
  justify-content: flex-start;
  align-items: flex-start;
  margin-top: 16px;
  margin-bottom: 4px;
  font-size: 36px;
  line-height: 41px;
  display: flex;
}

.features-list__text {
  font-family: var(--alternate-text-font-family);
  letter-spacing: .02em;
  font-size: 12px;
  font-weight: 700;
  line-height: 170%;
}

.features-list:last-child {
  padding-bottom: 39px;
}

